@use 'styles/colors';
@import 'styles/base';

:host {
  display: block;

  .top-section {
    max-width: 460px;
  }

  .page-title {
    @extend %page-title;
    padding-bottom: 20px;
  }

  .page-description {
    @extend %page-description;
    padding-top: 0;
  }

  .page-button {
    margin-top: 10px;
  }

  .page-card {
    @extend %page-card;
    margin-top: 30px;
    width: 100%;
    max-width: 540px;
  }

  .card-title {
    @extend %card-title;
    padding-bottom: 0;
  }

  .section-divider {
    @extend %section-divider;
  }

  .accounts-list {
    &__empty {
      @include proxima(16px, 400, colors.$foreground_dark)
    }

    &__item {
      display: flex;
      flex-wrap: wrap;
      padding: 14px;
      background-color: #f2f2f2;
      gap: 10px;

      &-section {
        display: flex;
        align-items: center;
        gap: 10px;

        &:first-child {
          flex-grow: 1;
          flex-shrink: 0;
          padding: 0 8px;
          background-color: #FFFFFF;
        }

      }

      &-mark {
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: #ee2a17;
        margin-right: 8px;
        flex-shrink: 0;
      }

      &-text {
        @include proxima(16px, 600, colors.$foreground_dark);
      }

      &:not(:last-child) {
        border-bottom: 1px solid #E8E8E8;
      }

      &:last-child {
        margin-bottom: 10px;
      }
    }
  }

  @media screen and (max-width: 1080px) {
    .accounts-list {
      &__item {
        flex-direction: column;

        &-section {
          &:first-child {
            padding: 0 8px;
          }
        }
      }
    }
  }
}
